  border-bottom-right-radius: 0.25rem;
  border-bottom-left-radius: 0.25rem;
  box-shadow: 0 2px 3px 0 rgba(0, 0, 0, 0.16);
}
.slds-modal__menu {
  position: relative;
  border-radius: 0.25rem;
  padding: 1rem;
  background-color: #f3f2f2;
}
